import { useState } from "react";
import {
  Mail,
  Loader,
  Phone,
  MapPin,
  Instagram,
  Send,
  PhoneCall,
} from "lucide-react";

const ContactUs = () => {
  const [isMapLoaded, setIsMapLoaded] = useState(false);

  return (
    <section
      className="bg-white py-16 px-4 text-right min-h-[61vh]"
      aria-labelledby="contact-us-section"
    >
      <div className="max-w-6xl mx-auto grid md:grid-cols-2 gap-12">
        {/* Contact Info */}
        <div>
          <h2
            id="contact-us-section"
            className="text-3xl font-bold text-blue-800 mb-6"
            aria-level="2"
          >
            تماس با ما
          </h2>
          <p className="text-gray-600 mb-6 leading-7">
            در صورت نیاز به مشاوره خرید یا پشتیبانی، تیم ما آماده پاسخگویی به
            شماست.
          </p>

          <ul className="space-y-5 text-gray-700">
            <li className="flex items-center gap-3">
              <PhoneCall className="text-blue-600" aria-hidden="true" />
              <a aria-label="شماره تماس" href="tel:9392566494" dir="ltr">
                09392566494
              </a>
            </li>
            <li className="flex items-center gap-3">
              <Mail className="text-blue-600" aria-hidden="true" />
              <span aria-label="ایمیل">shop.allmaxsport@gmail.com</span>
            </li>
            <li className="flex items-center gap-3">
              <MapPin className="text-blue-600" aria-hidden="true" />
              <span aria-label="آدرس دفتر">تهران، خیابان آزادی، پلاک ۴۲</span>
            </li>
          </ul>

          {/* Social Media */}
          <div className="mt-8">
            <h3 className="text-lg font-semibold text-gray-800 mb-3">
              ما را در شبکه‌های اجتماعی دنبال کنید
            </h3>
            <div className="flex gap-4 text-blue-600">
              <a
                href="https://instagram.com/allmaxsport"
                target="_blank"
                rel="noopener noreferrer"
                aria-label="اینستاگرام آلمکس اسپورت"
              >
                <Instagram className="hover:text-blue-800 transition" />
              </a>
              <a
                href="https://t.me/09392566494"
                target="_blank"
                rel="noopener noreferrer"
                aria-label="تلگرام آلمکس اسپورت"
              >
                <Send className="hover:text-blue-800 transition" />
              </a>
              <a
                href="https://wa.me/09392566494"
                target="_blank"
                rel="noopener noreferrer"
                aria-label="واتساپ آلمکس اسپورت"
              >
                <Phone className="hover:text-blue-800 transition" />
              </a>
            </div>
          </div>
        </div>

        {/* Google Map */}
        <div className="rounded-2xl overflow-hidden shadow-lg relative min-h-[350px]">
          {/* Loading Overlay */}
          {!isMapLoaded && (
            <div className="p-6 flex justify-center items-center h-64">
              <div className="flex flex-col items-center">
                <Loader className="w-10 h-10 text-blue-500 animate-spin mb-4" />
                <p className="text-gray-600">در حال بارگذاری اطلاعات نقشه...</p>
              </div>
            </div>
          )}

          <iframe
            title="موقعیت فروشگاه آلمکس اسپورت"
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3238.637199421837!2d51.34223077468972!3d35.700099727507384!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3f8e01d6bc3fa4db%3A0x25c92df5eb173a3f!2z2KfYs9iq2KfYsdin2LHYqSDZhNmE2KfYsdmE2Kkg2KfYs9iq2KfZhCDYrNmI2YTYp9iq!5e0!3m2!1sen!2s!4v1700000000000"
            width="100%"
            height="350"
            allowFullScreen
            loading="lazy"
            aria-label="دفتر آلمکس اسپورت"
            referrerPolicy="no-referrer-when-downgrade"
            className={`w-full h-full border-0 transition-opacity duration-300 ${
              isMapLoaded ? "opacity-100" : "opacity-0"
            }`}
            onLoad={() => setIsMapLoaded(true)}
          />
        </div>
      </div>

      {/* Schema Markup for SEO */}
      <script type="application/ld+json">
        {JSON.stringify({
          "@context": "https://schema.org",
          "@type": "LocalBusiness",
          name: "آلمکس اسپورت",
          image: "https://allmaxsport.com/logo.jpg",
          address: {
            "@type": "PostalAddress",
            addressLocality: "تهران",
            streetAddress: "خیابان آزادی، پلاک ۴۲",
            addressCountry: "IR",
          },
          telephone: "+98 939 256 6494",
          email: "info@gymstore.com",
          url: "https://allmaxsport.com",
          sameAs: [
            "https://instagram.com",
            "https://t.me",
            "https://wa.me/09392566494",
          ],
        })}
      </script>
    </section>
  );
};

export default ContactUs;
